<template>
  <div class="food-detail">
    <Navbar />
    <div class="container">
      <!-- breadcrumb -->
      <div class="row mt-4">
        <div class="col">
          <nav aria-label="breadcrumb">
            <ol class="breadcrumb">
              <li class="breadcrumb-item">
                <router-link to="/" class="text-dark">Home</router-link>
              </li>
              <li class="breadcrumb-item">
                <router-link to="/foods" class="text-dark">Foods</router-link>
              </li>
              <li class="breadcrumb-item active" aria-current="page">Food Order</li>
            </ol>
          </nav>
        </div>
      </div>

      <div class="row mt-3">
        <div class="col-md-6">
          <img :src=" '../assets/images/' + product.gambar " class="img-fluid shadow" />
        </div>
        <div class="col-md-6">
          <h2>
            <strong>{{ product.nama }}</strong>
          </h2>
          <hr />
          <h4>
            Price :
            <strong>Rp. {{ product.harga }}</strong>
          </h4>
          <form class="mt-4" v-on:submit.prevent>
            <div class="form-group">
              <label for="jumlah_pemesanan">Number of Orders</label>
              <input type="number" class="form-control" v-model="pesan.jumlah_pemesanan" />
            </div>
            <div class="form-group">
              <label for="keterangan">Notes</label>
              <textarea
                v-model="pesan.keterangan"
                class="form-control"
                placeholder="Keterangan spt : Pedes, Nasi Setengah .."
              ></textarea>
            </div>

            <button type="submit" class="btn btn-success" @click="pemesanan">
              <b-icon-cart></b-icon-cart>Order
            </button>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Navbar from "@/components/Navbar.vue";
import store from "../utils/store";

export default {
  name: "FoodDetail",
  components: {
    Navbar,
  },
  data() {
    return {
      product: {},
      pesan: {},
    };
  },
  methods: {
    setProduct(data) {
      this.product = data;
    },
    pemesanan() {
      if (this.pesan.jumlah_pemesanan) {
        this.pesan.products = this.product;
        // https://wa.me/nomorteleponwhatsapp/?text=teksberkodeurl
        if (this.pesan.keterangan == undefined) {
          this.pesan.keterangan = "";
        }
        var pesanWA = "Halo, saya mau memesan " + this.product.nama + " sebanyak " + this.pesan.jumlah_pemesanan +"Kg"+ ", " + this.pesan.keterangan;
        window.open("https://wa.me/6285376618044/?text=" + encodeURIComponent(JSON.stringify(pesanWA)), "_blank");
      } else {
        this.$toast.error("Jumlah Orderan Harus diisi", {
          type: "error",
          position: "top-right",
          duration: 3000,
          dismissible: true,
        });
      }
    },
  },
  mounted() {
    var paramsId = this.$route.params.id.replace(/[^a-zA-Z0-9.-]/g, '');
    console.log(paramsId);
    if(paramsId !== undefined) {
      if(paramsId === "gambir"){
        this.setProduct(store.state.products[0])
      }else if(paramsId === "talas"){
        this.setProduct(store.state.products[1])
      }
    }
  },
};
</script>

