import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
    state: {
        products : [
            { id: "gambir", nama: 'Daun Gambir', "harga": "20.000/Kg", gambar : "gambir.jpg" },
            { id: "talas", nama: 'Daun Talas', "harga": "35.000/Kg", gambar : "talas.jpg" },
        ]
    },
    mutations: {
        increment(state) {
            state.count++
        },
    },
    actions: {
        increment({ commit }) {
            commit('increment')
        }
    }
})

export default store
